@font-face {
  font-family: Inter;
  font-style: normal;
  font-weight: 400;
  src: local(""), url("../inter-v12-latin-regular.cb07b618.woff2") format("woff2"), url("../inter-v12-latin-regular.5f62869a.woff") format("woff");
}

* {
  margin: 0;
  padding: 0;
}

.chat-page__wrapper {
  height: 100vh;
  flex-direction: row;
  font-family: Inter, serif;
  font-style: normal;
  display: flex;
}

.chat-page__dialogues {
  width: 20%;
  background-color: #fbfbfb;
  border: 1px solid silver;
  flex-direction: column;
  order: 1;
  display: flex;
}

.chat-page__dialogues_right_text {
  justify-content: flex-end;
  display: flex;
}

.chat-page__dialogues__link {
  color: #999;
  justify-content: flex-end;
  align-self: flex-end;
  margin: 10px;
  font-size: 12px;
  font-weight: 500;
  line-height: 12px;
  text-decoration: none;
  display: flex;
  position: fixed;
}

.chat-page__dialogues_search {
  width: 87%;
  background-color: #efefef;
  border: none;
  border-radius: 5px;
  margin-top: 75px;
  margin-bottom: 10px;
  margin-left: 10px;
  padding: 10px;
  font-size: 12px;
  line-height: 12px;
}

.chat-page__dialogues_search::placeholder {
  color: #999;
}

.chat-page__dialogues_messages {
  border-top: 1px solid #eaeaea;
  border-bottom: 1px solid #eaeaea;
  flex-direction: row;
  display: flex;
}

.chat-page__dialogues_avatar {
  width: 47px;
  height: 47px;
  background-color: #efefef;
  border-radius: 50%;
  margin: 10px;
}

.chat-page__dialogues_name {
  margin: 5px;
  font-size: 13px;
  line-height: 13px;
}

.chat-page__dialogues_last-message {
  color: #999;
  margin: 5px;
  font-size: 12px;
  line-height: 15px;
}

.chat-page__dialogues_time {
  color: #999;
  flex: 2;
  justify-content: flex-end;
  margin: 5px;
  font-size: 9px;
  line-height: 13px;
  display: flex;
}

.chat-page__message {
  height: 100vh;
  border: 1px solid silver;
  flex-direction: column;
  flex: 2;
  order: 2;
  display: flex;
}

.chat-page__message-user {
  border-bottom: 2px solid #eaeaea;
  flex-direction: row;
  align-items: center;
  padding: 10px 0 0;
  display: flex;
}

.chat-page__message_avatar {
  width: 47px;
  height: 47px;
  background-color: #efefef;
  border-radius: 50%;
  margin: 10px;
}

.chat-page__message_name {
  margin: 5px;
  font-size: 13px;
  font-weight: 600;
  line-height: 13px;
}

.chat-page__message_history {
  height: 100%;
  border-bottom: 1px solid #eaeaea;
  flex-direction: column;
  padding: 20px 20px 10px 30px;
  display: flex;
}

.chat-page__message_history-time {
  color: #999;
  align-self: center;
  padding: 20px;
  font-size: 12px;
  line-height: 12px;
}

.chat-page__message-time {
  text-align: right;
  color: #999;
  font-size: 9px;
  line-height: 9px;
}

.chat-page__message_in {
  flex-direction: row;
}

.chat-page__message_in-message {
  width: 40%;
  text-align: left;
  background-color: #efefef;
  border-radius: 9px;
  align-self: flex-start;
  padding: 10px 10px 10px 30px;
}

.chat-page__message_out {
  flex-direction: row;
}

.chat-page__message_out-message {
  background-color: #e4edfd;
  border-radius: 9px;
  align-self: flex-end;
  padding: 10px;
}

.chat-page__message-input-wrapper {
  flex: 1;
  align-items: center;
  margin: 5px;
  display: flex;
}

.chat-page__message-input {
  width: 849px;
  height: 32px;
  background-color: #efefef;
  border: none;
  border-radius: 30px;
  flex-grow: 1;
  padding-left: 10px;
}

.chat-page__message-img {
  width: 20px;
  height: 20px;
  margin: 5px;
}

.chat-page__message-button {
  width: 28px;
  height: 28px;
  cursor: pointer;
  color: #fff;
  text-align: center;
  background-color: #3369f3;
  border: none;
  border-radius: 50%;
  margin: 5px;
  font-size: 20px;
  display: inline-block;
}

